'use client'
import { Cloud } from 'lucide-react'
import Link from 'next/link'
import React, { useState } from 'react'
import { Button } from './ui/button'
import { Menu } from 'lucide-react'
import { X } from 'lucide-react'

export default function Header() {
    const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  return (
    <header className=' sticky top-0 w-full px-4 py-5 border-b z-50 backdrop-blur-md'>
        <div className='max-w-7xl mx-auto flex justify-between items-center'>
            {/* Logo */}
            <Link href="/" className=''>
                <h1 className='text-2xl font-medium flex items-center gap-2'><span><Cloud size={34} className='text-primary'/></span>Cloud for Nothing</h1>
            </Link>

            {/* Desktop Nav */}
            <nav className='hidden md:flex items-center space-x-4'>
                <Link href="#about" className='hover:text-primary transition font-medium'>About</Link>
                <Link href="#features" className='hover:text-primary transition font-medium'>Features</Link>
                <Link href="#support" className='hover:text-primary transition font-medium'>Support üôè</Link>
                <Link href="#login" className='hover:text-primary transition font-medium'>Login</Link>
                <Button>
                    <Link href="/get-started">Get Started</Link>
                </Button>
            </nav>

            {/* Mobile nav */}
            <div className='block md:hidden'>
                {/* Mobile menu button */}
                <button onClick={()=>setIsMobileMenuOpen(!isMobileMenuOpen)} className='p-2 rounded-md focus:outline-none focus:ring-2 focus:ring-inset focus:ring-primary'>
                    {
                        isMobileMenuOpen ? <X size={24} /> :
                        <Menu size={24} />
                    }
                </button>
            </div>
            {isMobileMenuOpen &&
                <nav className={`md:hidden absolute top-16 left-0 w-full bg-background border-b p-4 space-y-4 flex flex-col`}>
                    <Link href="#about" className='hover:text-primary transition font-medium'>About</Link>
                    <Link href="#features" className='hover:text-primary transition font-medium'>Features</Link>
                    <Link href="#support" className='hover:text-primary transition font-medium'>Support üôè</Link>
                    <Link href="#login" className='hover:text-primary transition font-medium'>Login</Link>
                    <Button>
                        <Link href="/get-started">Get Started</Link>
                    </Button>
                </nav>
            }
        </div>
    </header>
  )
}
